import axios from "axios";
import uniqueString from "unique-string";

export const createUrl = async (url, slug = "") => {
  //Generate slug if not given
  let newSlug;
  slug === "" ? (newSlug = uniqueString()) : (newSlug = slug);

  try {
    const response = await axios.post("/urls", {
      url: url,
      slug: newSlug,
    });
    return response.data;
  } catch (err) {
    return err;
  }
};
