import React, { useState } from "react";
import "./App.css";
import styled from "styled-components/macro";

//Functions
import { createUrl } from "./functions";

function App() {
  const [showWarning, setShowWarning] = useState(false);
  const [warningText, setWarningText] = useState("");

  const handleUrlCreation = async (url, slug) => {
    const result = await createUrl(url, slug);
    if (result.code === 1) {
      setShowWarning(true);
      setWarningText(result.text);
    } else {
      setShowWarning(true);
      setWarningText(result.url);
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    let pattern = /((([A-Za-z]{3,9}:(?:\/\/)?)(?:[\-;:&=\+\$,\w]+@)?[A-Za-z0-9\.\-]+|(?:www\.|[\-;:&=\+\$,\w]+@)[A-Za-z0-9\.\-]+)((?:\/[\+~%\/\.\w\-_]*)?\??(?:[\-\+=&;%@\.\w_]*)#?(?:[\.\!\/\\\w]*))?)/; //eslint-disable-line
    const url = e.target.url.value;
    const slug = e.target.slug.value;

    if (pattern.test(url)) {
      setShowWarning(false);
      handleUrlCreation(url, slug);
    } else {
      setShowWarning(true);
      setWarningText("Invalid URL");
    }
  };

  return (
    <Container className="App">
      <h1>MINIMIZE</h1>
      <h2>Easy url Shortner</h2>
      <form onSubmit={(e) => handleSubmit(e)}>
        <label>Url</label>
        <input name="url" type="text"></input>
        <label>Slug (leave empty for auto generated slug)</label>
        <input name="slug" type="text"></input>
        <button type="submit">SUBMIT</button>
      </form>
      {showWarning ? <Warning warning={warningText} /> : null}
    </Container>
  );
}

const Warning = ({ warning }) => {
  return <p>{warning}</p>;
};

//Styling
const Container = styled.div`
  width: 20%;
  height: auto;
  margin: 30vh auto;
  padding: 15px;
  background-color: white;
  border-radius: 40px;
  box-shadow: 50px -50px lightblue, -50px 50px lightgreen, -50px -50px lightpink,
    50px 50px lightgoldenrodyellow;
  color: black;

  h1 {
    text-align: center;
  }

  h2 {
    text-align: center;
  }

  form {
    padding: 5px;

    label {
      display: block;
      text-align: center;
      padding: 10px;
    }

    input {
      display: block;
      padding: 5px;
      margin: 0 auto;
    }

    button {
      margin-top: 10px;
    }
  }
`;

export default App;
